/*STATISTICS*/
.statistics {
  margin-top: 100px;
  @include mobile {
    margin-top: 50px;
  }

  &__title {
    margin-bottom: 101px;
    font-family: $font-raleway;
    font-size: 2.25rem;
    font-weight: normal;
    color: $color-black;
    text-align: center;
  }

  &__container {
    display: flex;
    align-items: center;
    max-width: 800px;
    margin: 100px auto 120px;

    @include mobile {
      flex-direction: column;
      align-items: center;

    }
  }


  &__wrapper {
    margin-left: 185px;
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 160px;
    @include mobile {
      margin: 0 0 35px;
    }

  }

  &__wrapper:nth-child(1) {
    margin-left: 0;

  }

  &__circle {
    position: relative;
    width: 63px;
    height: 63px;
    border-radius: 50%;
  }

  .circle-red {
    background-color: $color-redish;
  }

  .circle-blue {
    background-color: $color-primary;
  }

  .circle-green {
    background-color: $color-green;
  }

  &__img {
    position: absolute;
  }

  .img--person {
    left: 45%;
    top: 0;
  }

  .img--eye {
    left: 30%;
    top: 25%
  }

  .img--catalog {
    left: 50%;
    top: 15%;
  }

  &__number {
    padding-top: 13px;
    padding-bottom: 19px;
    font-family: $font-raleway;
    font-weight: 800;
    font-size: 1.25rem;
    letter-spacing: 6px;
  }

  &__text {
    text-align: center;
    font-family: $font-raleway;
    font-weight: normal;
    font-size: 1.0625rem;
    color: $color-grey;
  }
}